<template>
  <div class="news-manage">
    <el-card>
      <template #header>
        <div class="card-header">
          <span></span>
          <div>
            <el-button 
              type="warning" 
              @click="batchAudit" 
              :disabled="selectedNews.length === 0" 
              v-if="user.role === 'ADMIN'"
            >
              
            </el-button>
            <el-button 
              type="danger" 
              @click="batchDelete" 
              :disabled="selectedNews.length === 0"
            >
              
            </el-button>
            <el-button type="success" @click="exportNews"></el-button>
            <el-button type="primary" @click="openAddDialog"></el-button>
          </div>
        </div>
      </template>

      <div class="search-bar">
        <el-row :gutter="20">
          <el-col :span="8">
            <el-input
              v-model="searchKeyword"
              placeholder=""
              clearable
              @keyup.enter="loadNews"
            >
              <template #append>
                <el-button @click="loadNews"></el-button>
              </template>
            </el-input>
          </el-col>
          <el-col :span="4">
            <el-select v-model="statusFilter" placeholder="" style="width: 100%" @change="loadNews">
              <el-option label="" value="" />
              <el-option label="" value="0" />
              <el-option label="" value="1" />
              <el-option label="" value="2" />
            </el-select>
          </el-col>
          <el-col :span="3">
            <el-select v-model="categoryFilter" placeholder="" style="width: 100%" @change="loadNews">
              <el-option label="" value="" />
              <el-option label="" value="" />
              <el-option label="" value="" />
              <el-option label="" value="" />
              <el-option label="" value="" />
            </el-select>
          </el-col>
          <el-col :span="3">
            <el-select v-model="authorFilter" placeholder="" style="width: 100%" @change="loadNews">
              <el-option label="" value="" />
              <el-option label="" value="mine" />
            </el-select>
          </el-col>
          <el-col :span="6">
            <el-date-picker
              v-model="createTimeRange"
              type="daterange"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              style="width: 100%"
              @change="loadNews"
            />
          </el-col>
          <el-col :span="2">
            <el-button @click="resetFilters"></el-button>
          </el-col>
        </el-row>
      </div>

      <el-table :data="newsList" style="width: 100%" v-loading="loading" @selection-change="handleSelectionChange">
        <template #empty>
          <div class="empty-data">
            <el-empty description="" />
          </div>
        </template>
        <el-table-column type="selection" width="55" :selectable="canSelect" />
        <el-table-column prop="title" label="" width="180" show-overflow-tooltip />
        <el-table-column label="" width="80">
          <template #default="scope">
            <img v-if="scope.row.coverImage" :src="getImageUrl(scope.row.coverImage)" style="width: 50px; height: 30px; object-fit: cover;" />
            <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column prop="summary" label="" width="150" show-overflow-tooltip />
        <el-table-column prop="category" label="" width="100" />
        <el-table-column prop="author" label="" width="120">
          <template #default="scope">
            <span :style="{ color: scope.row.authorId === user.id ? '#409EFF' : '' }">
              {{ scope.row.author }}
              <el-tag v-if="scope.row.authorId === user.id" size="small" type="primary"></el-tag>
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="status" label="" width="100">
          <template #default="scope">
            <el-tag :type="getStatusType(scope.row.status)">
              {{ getStatusText(scope.row.status) }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="viewCount" label="" width="100" />
        <el-table-column prop="createTime" label="" width="180" />
        <el-table-column label="" width="220">
          <template #default="scope">
            <el-button size="small" @click="viewNews(scope.row)"></el-button>
            <el-button 
              size="small" 
              @click="editNews(scope.row)" 
              v-if="canEdit(scope.row)"
            >
              
            </el-button>
            <el-button 
              size="small" 
              type="primary" 
              @click="auditNews(scope.row)" 
              v-if="canAudit(scope.row)"
            >
              
            </el-button>
            <el-button 
              size="small" 
              type="danger" 
              @click="deleteNews(scope.row)" 
              v-if="canDelete(scope.row)"
            >
              
            </el-button>
            <span 
              v-if="!canEdit(scope.row) && !canAudit(scope.row) && !canDelete(scope.row) && scope.row.authorId !== user.id" 
              style="color: #999; font-size: 12px;"
            >
              
            </span>
          </template>
        </el-table-column>
      </el-table>

      <div class="pagination">
        <el-pagination
          v-model:current-page="currentPage"
          v-model:page-size="pageSize"
          :total="total"
          layout="total, sizes, prev, pager, next"
          :page-sizes="[10, 20, 50, 100]"
          @current-change="loadNews"
          @size-change="handleSizeChange"
        />
      </div>
    </el-card>

    <!-- / -->
    <el-dialog v-model="showAddDialog" :title="editingNews ? '' : ''" width="800px">
      <el-form :model="newsForm" ref="newsFormRef" label-width="100px">
        <el-form-item label="">
          <el-input v-model="newsForm.title" />
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="newsForm.summary" type="textarea" :rows="3" />
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="newsForm.category" placeholder="" style="width: 100%">
            <el-option label="" value="" />
            <el-option label="" value="" />
            <el-option label="" value="" />
            <el-option label="" value="" />
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="newsForm.content" type="textarea" :rows="10" />
        </el-form-item>
        <el-form-item label="">
          <el-upload
            class="upload-demo"
            :action="uploadUrl"
            :show-file-list="false"
            :on-success="handleCoverSuccess"
            :before-upload="beforeImageUpload"
            accept="image/*"
          >
            <el-button size="small" type="primary"></el-button>
          </el-upload>
          <div v-if="newsForm.coverImage" style="margin-top: 10px;">
            <img :src="getImageUrl(newsForm.coverImage)" style="width: 100px; height: 60px; object-fit: cover;" />
            <el-button size="small" type="danger" @click="newsForm.coverImage = ''" style="margin-left: 10px;"></el-button>
          </div>
        </el-form-item>
        
        <el-form-item label="">
          <el-upload
            class="upload-demo"
            :action="uploadUrl"
            :show-file-list="true"
            :on-success="handleContentImageSuccess"
            :before-upload="beforeImageUpload"
            accept="image/*"
            multiple
          >
            <el-button size="small" type="primary"></el-button>
          </el-upload>
          <div class="upload-tip"></div>
        </el-form-item>
        
        <el-form-item label="">
          <el-upload
            class="upload-demo"
            :action="uploadUrl"
            :show-file-list="true"
            :on-success="handleVideoSuccess"
            :before-upload="beforeVideoUpload"
            accept="video/*"
          >
            <el-button size="small" type="primary"></el-button>
          </el-upload>
          <div class="upload-tip">mp4avimov50MB</div>
        </el-form-item>
        
        <el-form-item label="">
          <el-upload
            class="upload-demo"
            :action="uploadUrl"
            :show-file-list="true"
            :on-success="handleAttachmentSuccess"
            :before-upload="beforeAttachmentUpload"
            multiple
          >
            <el-button size="small" type="primary"></el-button>
          </el-upload>
          <div class="upload-tip">10MB</div>
        </el-form-item>
      </el-form>
      <template #footer>
        <el-button @click="showAddDialog = false"></el-button>
        <el-button type="primary" @click="saveNews"></el-button>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog v-model="showAuditDialog" title="" width="400px">
      <div style="margin-bottom: 20px;">
        <strong></strong>{{ auditingNews && auditingNews.title }}
      </div>
      <el-radio-group v-model="auditStatus">
        <el-radio :label="1"></el-radio>
        <el-radio :label="2"></el-radio>
      </el-radio-group>
      <template #footer>
        <el-button @click="showAuditDialog = false"></el-button>
        <el-button type="primary" @click="submitAudit"></el-button>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog v-model="showViewDialog" title="" width="800px">
      <div v-if="viewingNews">
        <div class="view-item">
          <strong></strong>{{ viewingNews.title }}
        </div>
        <div class="view-item">
          <strong></strong>{{ viewingNews.author }}
        </div>
        <div class="view-item">
          <strong></strong>
          <el-tag :type="getStatusType(viewingNews.status)">
            {{ getStatusText(viewingNews.status) }}
          </el-tag>
        </div>
        <div class="view-item">
          <strong></strong>{{ viewingNews.summary }}
        </div>
        <div class="view-item" v-if="viewingNews.coverImage">
          <strong></strong><br/>
          <img :src="getImageUrl(viewingNews.coverImage)" style="max-width: 300px; max-height: 200px; object-fit: cover;" />
        </div>
        <div class="view-item" v-if="viewingNews.contentImages && JSON.parse(viewingNews.contentImages || '[]').length > 0">
          <strong></strong><br/>
          <div style="display: flex; flex-wrap: wrap; gap: 10px;">
            <img 
              v-for="(img, index) in JSON.parse(viewingNews.contentImages)" 
              :key="index"
              :src="getImageUrl(img)" 
              style="width: 100px; height: 100px; object-fit: cover; border-radius: 4px;" 
            />
          </div>
        </div>
        <div class="view-item" v-if="viewingNews.videos && JSON.parse(viewingNews.videos || '[]').length > 0">
          <strong></strong><br/>
          <div style="display: flex; flex-wrap: wrap; gap: 10px;">
            <video 
              v-for="(video, index) in JSON.parse(viewingNews.videos)" 
              :key="index"
              :src="getImageUrl(video)" 
              controls
              style="width: 200px; height: 150px; border-radius: 4px;" 
            />
          </div>
        </div>
        <div class="view-item" v-if="viewingNews.attachments && JSON.parse(viewingNews.attachments || '[]').length > 0">
          <strong></strong><br/>
          <div style="display: flex; flex-wrap: wrap; gap: 10px;">
            <a 
              v-for="(attachment, index) in JSON.parse(viewingNews.attachments)" 
              :key="index"
              :href="getImageUrl(attachment)" 
              target="_blank"
              style="display: inline-block; padding: 5px 10px; background: #f0f0f0; border-radius: 4px; text-decoration: none; color: #333;"
            >
               {{ index + 1 }}
            </a>
          </div>
        </div>
        <div class="view-item">
          <strong></strong>
          <div class="content-box" v-html="viewingNews.content"></div>
        </div>
        <div class="view-item">
          <strong></strong>{{ viewingNews.viewCount || 0 }}
        </div>
        <div class="view-item">
          <strong></strong>{{ viewingNews.createTime }}
        </div>
      </div>
      <template #footer>
        <el-button @click="showViewDialog = false"></el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted, computed } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import request from '../utils/request'

const newsList = ref([])
const loading = ref(false)
const showAddDialog = ref(false)
const showAuditDialog = ref(false)
const showViewDialog = ref(false)
const editingNews = ref(null)
const auditingNews = ref(null)
const viewingNews = ref(null)
const searchKeyword = ref('')
const statusFilter = ref('')
const categoryFilter = ref('')
const authorFilter = ref('')
const createTimeRange = ref([])
const currentPage = ref(1)
const pageSize = ref(10)
const total = ref(0)
const auditStatus = ref(1)
const uploadUrl = ref('/api/file/upload')
const selectedNews = ref([])

const user = computed(() => {
  const userStr = localStorage.getItem('user')
  return userStr ? JSON.parse(userStr) : {}
})

const newsForm = reactive({
  title: '',
  summary: '',
  content: '',
  category: '',
  coverImage: '',
  contentImages: [],
  videos: [],
  attachments: []
})

const newsFormRef = ref()

onMounted(() => {
  loadNews()
})

const loadNews = async () => {
  loading.value = true
  try {
    const params = {
      current: currentPage.value,
      size: pageSize.value,
      keyword: searchKeyword.value
    }
    
    // 
    if (statusFilter.value !== '') {
      params.status = statusFilter.value
    }
    
    // 
    if (categoryFilter.value !== '') {
      params.category = categoryFilter.value
    }
    
    // 
    if (createTimeRange.value && createTimeRange.value.length === 2) {
      const startDate = createTimeRange.value[0]
      const endDate = createTimeRange.value[1]
      
      if (startDate instanceof Date) {
        params.startDate = startDate.toISOString().split('T')[0]
      } else {
        params.startDate = startDate
      }
      
      if (endDate instanceof Date) {
        params.endDate = endDate.toISOString().split('T')[0]
      } else {
        params.endDate = endDate
      }
    }
    
    // 
    if (authorFilter.value === 'mine') {
      params.authorId = user.value.id
    }

    const res = await request.get('/news/page', { params })
    newsList.value = res.data.records || []
    total.value = res.data.total || 0
  } catch (error) {
    console.error(':', error)
    newsList.value = []
  } finally {
    loading.value = false
  }
}

const getStatusType = (status) => {
  const types = { 0: 'warning', 1: 'success', 2: 'danger' }
  return types[status] || 'info'
}

const getStatusText = (status) => {
  const texts = { 0: '', 1: '', 2: '' }
  return texts[status] || ''
}

const canEdit = (news) => {
  return user.value.role === 'ADMIN' || news.authorId === user.value.id
}

const canAudit = (news) => {
  return user.value.role === 'ADMIN' && news.status === 0
}

const canDelete = (news) => {
  return user.value.role === 'ADMIN' || news.authorId === user.value.id
}

const canSelect = (row) => {
  // 
  return user.value.role === 'ADMIN' || row.authorId === user.value.id
}

const viewNews = (news) => {
  viewingNews.value = news
  showViewDialog.value = true
}

const editNews = (news) => {
  editingNews.value = news
  Object.assign(newsForm, {
    ...news,
    contentImages: news.contentImages ? JSON.parse(news.contentImages) : [],
    videos: news.videos ? JSON.parse(news.videos) : [],
    attachments: news.attachments ? JSON.parse(news.attachments) : []
  })
  showAddDialog.value = true
}

const saveNews = async () => {
  try {
    // 
    if (!newsForm.title.trim()) {
      ElMessage.error('')
      return
    }
    if (!newsForm.content.trim()) {
      ElMessage.error('')
      return
    }
    if (!newsForm.category) {
      ElMessage.error('')
      return
    }
    
    // 
    const formData = {
      ...newsForm,
      contentImages: JSON.stringify(newsForm.contentImages),
      videos: JSON.stringify(newsForm.videos),
      attachments: JSON.stringify(newsForm.attachments)
    }
    
    if (editingNews.value) {
      await request.put(`/news/${editingNews.value.id}`, formData)
      ElMessage.success('')
    } else {
      await request.post('/news', formData)
      ElMessage.success('')
    }
    showAddDialog.value = false
    resetForm()
    loadNews()
  } catch (error) {
    console.error(':', error)
    ElMessage.error('')
  }
}

const auditNews = (news) => {
  auditingNews.value = news
  auditStatus.value = 1
  showAuditDialog.value = true
}

const submitAudit = async () => {
  try {
    await request.post(`/news/${auditingNews.value.id}/audit`, {
      status: auditStatus.value
    })
    ElMessage.success('')
    showAuditDialog.value = false
    loadNews()
  } catch (error) {
    console.error(':', error)
  }
}

const deleteNews = async (news) => {
  try {
    await ElMessageBox.confirm(
      `"${news.title}"`, 
      '', 
      {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        confirmButtonClass: 'el-button--danger'
      }
    )
    await request.delete(`/news/${news.id}`)
    ElMessage.success('')
    loadNews()
  } catch (error) {
    if (error !== 'cancel') {
      console.error(':', error)
      ElMessage.error('')
    }
  }
}

const resetForm = () => {
  editingNews.value = null
  Object.assign(newsForm, {
    title: '',
    summary: '',
    content: '',
    category: '',
    coverImage: '',
    contentImages: [],
    videos: [],
    attachments: []
  })
}

// 
const resetFilters = () => {
  searchKeyword.value = ''
  statusFilter.value = ''
  categoryFilter.value = ''
  authorFilter.value = ''
  createTimeRange.value = []
  currentPage.value = 1
  loadNews()
}

// 
const openAddDialog = () => {
  resetForm()
  showAddDialog.value = true
}

const handleCoverSuccess = (response) => {
  if (response.code === 200) {
    newsForm.coverImage = response.data
    ElMessage.success('')
  } else {
    ElMessage.error('')
  }
}

const beforeImageUpload = (file) => {
  const isImage = file.type.indexOf('image/') === 0
  const isLt2M = file.size / 1024 / 1024 < 2

  if (!isImage) {
    ElMessage.error('!')
    return false
  }
  if (!isLt2M) {
    ElMessage.error(' 2MB!')
    return false
  }
  return true
}

const beforeVideoUpload = (file) => {
  const isVideo = file.type.indexOf('video/') === 0
  const isLt50M = file.size / 1024 / 1024 < 50

  if (!isVideo) {
    ElMessage.error('!')
    return false
  }
  if (!isLt50M) {
    ElMessage.error(' 50MB!')
    return false
  }
  return true
}

const beforeAttachmentUpload = (file) => {
  const isLt10M = file.size / 1024 / 1024 < 10

  if (!isLt10M) {
    ElMessage.error(' 10MB!')
    return false
  }
  return true
}

const handleContentImageSuccess = (response) => {
  if (response.code === 200) {
    newsForm.contentImages.push(response.data)
    ElMessage.success('' + getImageUrl(response.data))
  } else {
    ElMessage.error('')
  }
}

const handleVideoSuccess = (response) => {
  if (response.code === 200) {
    newsForm.videos.push(response.data)
    ElMessage.success('')
  } else {
    ElMessage.error('')
  }
}

const handleAttachmentSuccess = (response) => {
  if (response.code === 200) {
    newsForm.attachments.push(response.data)
    ElMessage.success('')
  } else {
    ElMessage.error('')
  }
}

const getImageUrl = (imagePath) => {
  if (!imagePath) return ''
  if (imagePath.startsWith('http')) return imagePath
  return `http://localhost:8080/api${imagePath}`
}

const exportNews = async () => {
  try {
    const params = {
      keyword: searchKeyword.value
    }
    
    if (statusFilter.value !== '') {
      params.status = statusFilter.value
    }
    
    if (categoryFilter.value !== '') {
      params.category = categoryFilter.value
    }
    
    if (authorFilter.value === 'mine') {
      params.authorId = user.value.id
    }
    
    const res = await request.get('/news/export', {
      params,
      responseType: 'blob'
    })
    
    const blob = new Blob([res], { type: 'text/csv;charset=utf-8' })
    const url = window.URL.createObjectURL(blob)
    const a = document.createElement('a')
    a.href = url
    a.download = `news_${new Date().toISOString().split('T')[0]}.csv`
    document.body.appendChild(a)
    a.click()
    document.body.removeChild(a)
    window.URL.revokeObjectURL(url)
    
    ElMessage.success('')
  } catch (error) {
    console.error(':', error)
    ElMessage.error('')
  }
}

const handleSizeChange = (size) => {
  pageSize.value = size
  currentPage.value = 1
  loadNews()
}

const handleSelectionChange = (selected) => {
  selectedNews.value = selected
}

const batchAudit = async () => {
  try {
    // 
    if (user.value.role !== 'ADMIN') {
      ElMessage.error('')
      return
    }
    
    await ElMessageBox.confirm('', '')
    const ids = selectedNews.value.map(news => news.id)
    await request.post('/news/batch/audit', {
      ids,
      status: 1
    })
    ElMessage.success('')
    selectedNews.value = []
    loadNews()
  } catch (error) {
    if (error !== 'cancel') {
      console.error(':', error)
      ElMessage.error('')
    }
  }
}

const batchDelete = async () => {
  try {
    // 
    const unauthorizedNews = selectedNews.value.filter(news => 
      user.value.role !== 'ADMIN' && news.authorId !== user.value.id
    )
    
    if (unauthorizedNews.length > 0) {
      ElMessage.error(`${unauthorizedNews.length}`)
      return
    }
    
    await ElMessageBox.confirm(
      `${selectedNews.value.length}`, 
      '', 
      {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        confirmButtonClass: 'el-button--danger'
      }
    )
    const ids = selectedNews.value.map(news => news.id)
    await request.delete('/news/batch', {
      data: { ids }
    })
    ElMessage.success('')
    selectedNews.value = []
    loadNews()
  } catch (error) {
    if (error !== 'cancel') {
      console.error(':', error)
      ElMessage.error('')
    }
  }
}
</script>

<style scoped>
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.search-bar {
  margin-bottom: 20px;
}

.search-bar .el-row {
  align-items: center;
}

.upload-tip {
  color: #999;
  font-size: 12px;
  margin-top: 5px;
}

.pagination {
  margin-top: 20px;
  text-align: right;
}

.empty-data {
  padding: 40px 0;
}

.view-item {
  margin-bottom: 15px;
}

.content-box {
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  padding: 10px;
  background-color: #f5f7fa;
  max-height: 300px;
  overflow-y: auto;
}

.search-bar {
  margin-bottom: 20px;
  display: flex;
  align-items: center;
}

.pagination {
  margin-top: 20px;
  text-align: right;
}

.view-item {
  margin-bottom: 15px;
  line-height: 1.6;
}

.content-box {
  margin-top: 10px;
  padding: 15px;
  background-color: #f9f9f9;
  border-radius: 4px;
  white-space: pre-wrap;
  max-height: 300px;
  overflow-y: auto;
}

.empty-data {
  padding: 40px 0;
}
</style> 