<template>
  <div class="profile">
    <el-row :gutter="20">
      <el-col :span="8">
        <el-card>
          <template #header>
            <span></span>
          </template>
          <div class="user-info">
            <el-avatar :size="80" :src="getImageUrl(user.avatar)">
              {{ user.nickname || user.username }}
            </el-avatar>
            <el-upload
              class="avatar-uploader"
              :action="uploadUrl"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload"
            >
              <el-button size="small" type="text"></el-button>
            </el-upload>
            <h3>{{ user.nickname || user.username }}</h3>
            <p>{{ user.role === 'ADMIN' ? '' : '' }}</p>
            <p>{{ user.companyName }}</p>
            <p>{{ user.createTime }}</p>
          </div>
        </el-card>
      </el-col>
      
      <el-col :span="16">
        <el-card>
          <template #header>
            <span></span>
          </template>
          <el-form :model="profileForm" ref="profileFormRef" label-width="100px">
            <el-form-item label="">
              <el-input v-model="profileForm.username" disabled />
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="profileForm.nickname" placeholder="" />
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="profileForm.phone" />
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="profileForm.email" />
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="profileForm.gender" style="width: 200px">
                <el-option label="" :value="0" />
                <el-option label="" :value="1" />
                <el-option label="" :value="2" />
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="profileForm.companyName" />
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="updateProfile" :loading="updating"></el-button>
            </el-form-item>
          </el-form>
        </el-card>

        <el-card style="margin-top: 20px;">
          <template #header>
            <span></span>
          </template>
          <el-form :model="passwordForm" ref="passwordFormRef" label-width="100px">
            <el-form-item label="">
              <el-input v-model="passwordForm.oldPassword" type="password" show-password />
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="passwordForm.newPassword" type="password" show-password />
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="passwordForm.confirmPassword" type="password" show-password />
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="changePassword" :loading="changingPassword"></el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted } from 'vue'
import { ElMessage } from 'element-plus'
import request from '../utils/request'

const user = ref({})
const updating = ref(false)
const changingPassword = ref(false)
const uploadUrl = ref('/api/file/upload')

const profileForm = reactive({
  username: '',
  nickname: '',
  phone: '',
  email: '',
  gender: 0,
  companyName: ''
})

const passwordForm = reactive({
  oldPassword: '',
  newPassword: '',
  confirmPassword: ''
})

const profileFormRef = ref()
const passwordFormRef = ref()

onMounted(() => {
  loadUserInfo()
})

const loadUserInfo = async () => {
  try {
    // 
    const userRes = await request.get('/user/current')
    if (userRes.code === 200) {
      user.value = userRes.data
      localStorage.setItem('user', JSON.stringify(userRes.data))
      Object.assign(profileForm, userRes.data)
    }
  } catch (error) {
    // 
    console.error(':', error)
    const userStr = localStorage.getItem('user')
    if (userStr) {
      user.value = JSON.parse(userStr)
      Object.assign(profileForm, user.value)
    }
  }
}

const updateProfile = async () => {
  updating.value = true
  try {
    await request.put('/user/profile', profileForm)
    ElMessage.success('')
    // 
    const userRes = await request.get('/user/current')
    if (userRes.code === 200) {
      user.value = userRes.data
      localStorage.setItem('user', JSON.stringify(userRes.data))
      Object.assign(profileForm, userRes.data)
      // 
      window.dispatchEvent(new CustomEvent('userUpdated', { detail: userRes.data }))
    }
  } catch (error) {
    console.error(':', error)
  } finally {
    updating.value = false
  }
}

const changePassword = async () => {
  if (passwordForm.newPassword !== passwordForm.confirmPassword) {
    ElMessage.error('')
    return
  }
  
  if (passwordForm.newPassword.length < 6) {
    ElMessage.error('6')
    return
  }

  changingPassword.value = true
  try {
    await request.post('/user/changePassword', {
      oldPassword: passwordForm.oldPassword,
      newPassword: passwordForm.newPassword
    })
    ElMessage.success('')
    // 
    Object.assign(passwordForm, {
      oldPassword: '',
      newPassword: '',
      confirmPassword: ''
    })
  } catch (error) {
    console.error(':', error)
  } finally {
    changingPassword.value = false
  }
}

const handleAvatarSuccess = async (response) => {
  if (response.code === 200) {
    // 
    try {
      await request.put('/user/profile', { avatar: response.data })
      // 
      const userRes = await request.get('/user/current')
      if (userRes.code === 200) {
        user.value = userRes.data
        localStorage.setItem('user', JSON.stringify(userRes.data))
        // 
        window.dispatchEvent(new CustomEvent('userUpdated', { detail: userRes.data }))
      }
      ElMessage.success('')
    } catch (error) {
      console.error(':', error)
    }
  } else {
    ElMessage.error('')
  }
}

const beforeAvatarUpload = (file) => {
  const isImage = file.type.indexOf('image/') === 0
  const isLt2M = file.size / 1024 / 1024 < 2

  if (!isImage) {
    ElMessage.error('!')
    return false
  }
  if (!isLt2M) {
    ElMessage.error(' 2MB!')
    return false
  }
  return true
}

const getImageUrl = (imagePath) => {
  if (!imagePath) return ''
  if (imagePath.startsWith('http')) return imagePath
  return `http://localhost:8080/api${imagePath}`
}
</script>

<style scoped>
.user-info {
  text-align: center;
  padding: 20px;
}

.user-info h3 {
  margin: 15px 0 5px 0;
  color: #333;
}

.user-info p {
  margin: 5px 0;
  color: #666;
}

.avatar-uploader {
  margin: 10px 0;
}
</style> 