<template>
  <div class="user-manage">
    <el-card>
      <template #header>
        <div class="card-header">
          <span></span>
          <div>
            <el-button type="info" @click="loadUsers" :loading="loading">
              <el-icon><Refresh /></el-icon>
              
            </el-button>
            <el-button type="danger" @click="batchDelete" :disabled="selectedUsers.length === 0"></el-button>
            <el-button type="success" @click="exportUsers"></el-button>
            <el-button type="primary" @click="showAddDialog = true"></el-button>
          </div>
        </div>
      </template>

      <!--  -->
      <div class="search-bar">
        <el-row :gutter="20">
          <el-col :span="4">
            <el-input
              v-model="searchKeyword"
              placeholder=""
              clearable
              @keyup.enter="loadUsers"
            />
          </el-col>
          <el-col :span="4">
            <el-select v-model="roleFilter" placeholder="" clearable>
              <el-option label="" value="" />
              <el-option label="" value="ADMIN" />
              <el-option label="" value="USER" />
            </el-select>
          </el-col>
          <el-col :span="4">
            <el-select v-model="statusFilter" placeholder="" clearable>
              <el-option label="" value="" />
              <el-option label="" :value="1" />
              <el-option label="" :value="0" />
            </el-select>
          </el-col>
          <el-col :span="6">
            <el-date-picker
              v-model="dateRange"
              type="daterange"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              @change="loadUsers"
            />
          </el-col>
          <el-col :span="4" style="margin-left: 40px">
            <el-button type="primary" @click="loadUsers"></el-button>
            <el-button @click="resetSearch"></el-button>
          </el-col>
        </el-row>
      </div>

      <!--  -->
      <el-table :data="users" style="width: 100%" v-loading="loading" @selection-change="handleSelectionChange">
        <template #empty>
          <div class="empty-data">
            <el-empty description="" />
          </div>
        </template>
        <el-table-column type="selection" width="55" />
        <el-table-column label="" width="80">
          <template #default="scope">
            <el-avatar :size="40" :src="getImageUrl(scope.row.avatar)">
              {{ scope.row.nickname || scope.row.username }}
            </el-avatar>
          </template>
        </el-table-column>
        <el-table-column prop="username" label="" width="120" />
        <el-table-column prop="nickname" label="" width="120" />
        <el-table-column prop="phone" label="" width="130" />
        <el-table-column prop="companyName" label="" width="150" />
        <el-table-column prop="role" label="" width="100">
          <template #default="scope">
            <el-tag :type="scope.row.role === 'ADMIN' ? 'danger' : 'primary'">
              {{ scope.row.role === 'ADMIN' ? '' : '' }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="status" label="" width="100">
          <template #default="scope">
            <el-tag :type="scope.row.status === 1 ? 'success' : 'danger'">
              {{ scope.row.status === 1 ? '' : '' }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="createTime" label="" width="180" />
        <el-table-column label="" width="250">
          <template #default="scope">
            <el-button size="small" @click="viewUser(scope.row)"></el-button>
            <el-button size="small" @click="editUser(scope.row)"></el-button>
            <el-button size="small" type="danger" @click="deleteUser(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <div class="pagination">
        <el-pagination
          v-model:current-page="currentPage"
          v-model:page-size="pageSize"
          :total="total"
          layout="total, sizes, prev, pager, next"
          :page-sizes="[10, 20, 50, 100]"
          @current-change="loadUsers"
          @size-change="handleSizeChange"
        />
      </div>
    </el-card>

    <!-- / -->
    <el-dialog v-model="showAddDialog" title="" width="500px">
      <el-form :model="userForm" ref="userFormRef" label-width="100px">
        <el-form-item label="">
          <el-input v-model="userForm.username" />
        </el-form-item>
        <el-form-item label="" v-if="!editingUser">
          <el-input v-model="userForm.password" type="password" />
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="userForm.nickname" />
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="userForm.phone" />
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="userForm.companyName" />
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="userForm.role" style="width: 100%">
            <el-option label="" value="USER" />
            <el-option label="" value="ADMIN" />
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="userForm.status" style="width: 100%">
            <el-option label="" :value="1" />
            <el-option label="" :value="0" />
          </el-select>
        </el-form-item>
      </el-form>
      <template #footer>
        <el-button @click="showAddDialog = false"></el-button>
        <el-button type="primary" @click="saveUser"></el-button>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog v-model="showViewDialog" title="" width="500px">
      <div v-if="viewingUser">
        <div class="view-item">
          <strong></strong><br/>
          <el-avatar :size="80" :src="getImageUrl(viewingUser.avatar)" style="margin-top: 10px;">
            {{ viewingUser.nickname || viewingUser.username }}
          </el-avatar>
        </div>
        <div class="view-item">
          <strong></strong>{{ viewingUser.username }}
        </div>
        <div class="view-item">
          <strong></strong>{{ viewingUser.nickname || '' }}
        </div>
        <div class="view-item">
          <strong></strong>{{ viewingUser.phone || '' }}
        </div>
        <div class="view-item">
          <strong></strong>{{ viewingUser.email || '' }}
        </div>
        <div class="view-item">
          <strong></strong>{{ viewingUser.companyName || '' }}
        </div>
        <div class="view-item">
          <strong></strong>
          <el-tag :type="viewingUser.role === 'ADMIN' ? 'danger' : 'primary'">
            {{ viewingUser.role === 'ADMIN' ? '' : '' }}
          </el-tag>
        </div>
        <div class="view-item">
          <strong></strong>
          <el-tag :type="viewingUser.status === 1 ? 'success' : 'danger'">
            {{ viewingUser.status === 1 ? '' : '' }}
          </el-tag>
        </div>
        <div class="view-item">
          <strong></strong>{{ viewingUser.gender === 1 ? '' : viewingUser.gender === 2 ? '' : '' }}
        </div>
        <div class="view-item">
          <strong></strong>{{ viewingUser.createTime }}
        </div>
      </div>
      <template #footer>
        <el-button @click="showViewDialog = false"></el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { Refresh } from '@element-plus/icons-vue'
import request from '../utils/request'

const users = ref([])
const loading = ref(false)
const showAddDialog = ref(false)
const showViewDialog = ref(false)
const editingUser = ref(null)
const viewingUser = ref(null)
const searchKeyword = ref('')
const currentPage = ref(1)
const pageSize = ref(10)
const total = ref(0)
const selectedUsers = ref([])
const roleFilter = ref('')
const statusFilter = ref('')
const dateRange = ref([])

const userForm = reactive({
  username: '',
  password: '',
  nickname: '',
  phone: '',
  companyName: '',
  role: 'USER',
  status: 1
})

const userFormRef = ref()

onMounted(() => {
  loadUsers()
})

const loadUsers = async () => {
  loading.value = true
  try {
    const res = await request.get('/user/page', {
      params: {
        current: currentPage.value,
        size: pageSize.value,
        keyword: searchKeyword.value,
        role: roleFilter.value,
        status: statusFilter.value,
        startDate: dateRange.value[0] ? dateRange.value[0].toISOString().split('T')[0] : null,
        endDate: dateRange.value[1] ? dateRange.value[1].toISOString().split('T')[0] : null
      }
    })
    users.value = res.data.records || []
    total.value = res.data.total || 0
  } catch (error) {
    console.error(':', error)
    users.value = []
  } finally {
    loading.value = false
  }
}

const viewUser = (user) => {
  viewingUser.value = user
  showViewDialog.value = true
}

const editUser = (user) => {
  editingUser.value = user
  Object.assign(userForm, user)
  showAddDialog.value = true
}

const saveUser = async () => {
  try {
    if (editingUser.value) {
      await request.put(`/user/${editingUser.value.id}`, userForm)
      ElMessage.success('')
    } else {
      await request.post('/user', userForm)
      ElMessage.success('')
    }
    showAddDialog.value = false
    resetForm()
    loadUsers()
  } catch (error) {
    console.error(':', error)
  }
}

const deleteUser = async (user) => {
  try {
    await ElMessageBox.confirm('', '')
    await request.delete(`/user/${user.id}`)
    ElMessage.success('')
    loadUsers()
  } catch (error) {
    if (error !== 'cancel') {
      console.error(':', error)
    }
  }
}

const resetForm = () => {
  editingUser.value = null
  Object.assign(userForm, {
    username: '',
    password: '',
    nickname: '',
    phone: '',
    companyName: '',
    role: 'USER',
    status: 1
  })
}

const getImageUrl = (imagePath) => {
  if (!imagePath) return ''
  if (imagePath.startsWith('http')) return imagePath
  return `http://localhost:8080/api${imagePath}`
}

const exportUsers = async () => {
  try {
    const res = await request.get('/user/export', {
      params: {
        keyword: searchKeyword.value,
        role: roleFilter.value,
        status: statusFilter.value
      },
      responseType: 'blob'
    })
    
    const blob = new Blob([res], { type: 'text/csv;charset=utf-8' })
    const url = window.URL.createObjectURL(blob)
    const a = document.createElement('a')
    a.href = url
    a.download = `users_${new Date().toISOString().split('T')[0]}.csv`
    document.body.appendChild(a)
    a.click()
    document.body.removeChild(a)
    window.URL.revokeObjectURL(url)
    
    ElMessage.success('')
  } catch (error) {
    console.error(':', error)
    ElMessage.error('')
  }
}

const handleSizeChange = (size) => {
  pageSize.value = size
  currentPage.value = 1
  loadUsers()
}

const handleSelectionChange = (selection) => {
  selectedUsers.value = selection.map(user => user.id)
}

const batchDelete = async () => {
  try {
    await ElMessageBox.confirm('', '')
    await request.delete('/user/batch', {
      data: {
        ids: selectedUsers.value
      }
    })
    ElMessage.success('')
    loadUsers()
  } catch (error) {
    if (error !== 'cancel') {
      console.error(':', error)
    }
  }
}

const resetSearch = () => {
  searchKeyword.value = ''
  roleFilter.value = ''
  statusFilter.value = ''
  dateRange.value = []
  loadUsers()
}
</script>

<style scoped>
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.search-bar {
  margin-bottom: 20px;
}

.pagination {
  margin-top: 20px;
  text-align: right;
}

.view-item {
  margin-bottom: 15px;
  line-height: 1.6;
}

.empty-data {
  padding: 40px 0;
}
</style> 