<template>
  <div class="meeting-manage">
    <el-card>
      <template #header>
        <div class="card-header">
          <span></span>
          <div>
            <el-button type="success" @click="exportMeetings"></el-button>
            <el-button type="primary" @click="showAddDialog = true"></el-button>
          </div>
        </div>
      </template>

      <div class="search-bar">
        <el-row :gutter="20">
          <el-col :span="8">
            <el-input
              v-model="searchKeyword"
              placeholder=""
              clearable
              @keyup.enter="loadMeetings"
            >
              <template #append>
                <el-button @click="loadMeetings"></el-button>
              </template>
            </el-input>
          </el-col>
          <el-col :span="4">
            <el-select v-model="statusFilter" placeholder="" style="width: 100%" @change="loadMeetings">
              <el-option label="" value="" />
              <el-option label="" value="0" />
              <el-option label="" value="1" />
              <el-option label="" value="2" />
            </el-select>
          </el-col>
          <el-col :span="6">
            <el-date-picker
              v-model="createTimeRange"
              type="daterange"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              format="YYYY-MM-DD"
              value-format="YYYY-MM-DD"
              style="width: 100%"
              @change="loadMeetings"
            />
          </el-col>
          <el-col :span="6">
            <el-button @click="resetFilters" style="margin-right: 10px"></el-button>
            <el-button type="primary" @click="openNotifyDialog" v-if="isAdmin"></el-button>
          </el-col>
        </el-row>
      </div>

      <el-table :data="meetingList" style="width: 100%" v-loading="loading">
        <template #empty>
          <div class="empty-data">
            <el-empty description="" />
          </div>
        </template>
        <el-table-column prop="meetingName" label="" width="180" show-overflow-tooltip />
        <el-table-column label="" width="80">
          <template #default="scope">
            <img v-if="scope.row.coverImage" :src="getImageUrl(scope.row.coverImage)" style="width: 50px; height: 30px; object-fit: cover;" />
            <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column prop="meetingTime" label="" width="160" />
        <el-table-column prop="location" label="" width="150" />
        <el-table-column prop="organizer" label="" width="120" />
        <el-table-column prop="category" label="" width="120" />
        <el-table-column prop="creator" label="" width="100" />
        <el-table-column prop="status" label="" width="100">
          <template #default="scope">
            <el-tag :type="getStatusType(scope.row.status)">
              {{ getStatusText(scope.row.status) }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="createTime" label="" width="180" />
        <el-table-column label="" width="250">
          <template #default="scope">
            <el-button 
              size="small" 
              @click="editMeeting(scope.row)" 
              v-if="canEdit(scope.row)"
            >
              
            </el-button>
            <el-button 
              size="small" 
              type="primary" 
              @click="auditMeeting(scope.row)" 
              v-if="canAudit(scope.row)"
            >
              
            </el-button>
            <el-button 
              size="small" 
              type="danger" 
              @click="deleteMeeting(scope.row)" 
              v-if="canDelete(scope.row)"
            >
              
            </el-button>
            <span v-if="!canEdit(scope.row) && !canAudit(scope.row) && !canDelete(scope.row)" style="color: #999;">
              
            </span>
          </template>
        </el-table-column>
      </el-table>

      <div class="pagination">
        <el-pagination
          v-model:current-page="currentPage"
          v-model:page-size="pageSize"
          :total="total"
          layout="total, sizes, prev, pager, next"
          :page-sizes="[10, 20, 50, 100]"
          @current-change="loadMeetings"
          @size-change="handleSizeChange"
        />
      </div>
    </el-card>

    <!-- / -->
    <el-dialog v-model="showAddDialog" :title="editingMeeting ? '' : ''" width="600px">
      <el-form :model="meetingForm" ref="meetingFormRef" label-width="100px">
        <el-form-item label="">
          <el-input v-model="meetingForm.meetingName" />
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="meetingForm.meetingTime"
            type="datetime"
            placeholder=""
            style="width: 100%"
          />
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="meetingForm.location" />
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="meetingForm.organizer" />
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="meetingForm.category" style="width: 100%">
            <el-option label="" value="" />
            <el-option label="" value="" />
            <el-option label="" value="" />
            <el-option label="" value="" />
            <el-option label="" value="" />
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="meetingForm.meetingContent" type="textarea" :rows="6" />
        </el-form-item>
        <el-form-item label="">
          <el-upload
            class="upload-demo"
            :action="uploadUrl"
            :show-file-list="false"
            :on-success="handleCoverSuccess"
            :before-upload="beforeUpload"
          >
            <el-button size="small" type="primary"></el-button>
          </el-upload>
          <div v-if="meetingForm.coverImage" style="margin-top: 10px;">
            <img :src="getImageUrl(meetingForm.coverImage)" style="width: 100px; height: 60px; object-fit: cover;" />
            <el-button size="small" type="danger" @click="meetingForm.coverImage = ''" style="margin-left: 10px;"></el-button>
          </div>
        </el-form-item>
      </el-form>
      <template #footer>
        <el-button @click="showAddDialog = false"></el-button>
        <el-button type="primary" @click="saveMeeting"></el-button>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog v-model="showAuditDialog" title="" width="400px">
      <div style="margin-bottom: 20px;">
        <strong></strong>{{ auditingMeeting && auditingMeeting.meetingName }}
      </div>
      <el-radio-group v-model="auditStatus">
        <el-radio :label="1"></el-radio>
        <el-radio :label="2"></el-radio>
      </el-radio-group>
      <template #footer>
        <el-button @click="showAuditDialog = false"></el-button>
        <el-button type="primary" @click="submitAudit"></el-button>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog v-model="showNotifyDialog" title="" width="600px">
      <el-form :model="notifyForm" ref="notifyFormRef" label-width="100px">
        <el-form-item label="" required>
          <el-input v-model="notifyForm.title" placeholder="" />
        </el-form-item>
        <el-form-item label="" required>
          <el-input 
            v-model="notifyForm.content" 
            type="textarea" 
            :rows="8"
            placeholder=""
          />
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="notifyForm.meetingId" placeholder="" style="width: 100%">
            <el-option 
              v-for="meeting in publishedMeetings" 
              :key="meeting.id" 
              :label="meeting.meetingName" 
              :value="meeting.id" 
            />
          </el-select>
        </el-form-item>
      </el-form>
      <template #footer>
        <el-button @click="showNotifyDialog = false"></el-button>
        <el-button type="primary" @click="publishNotify"></el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted, computed } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import request from '../utils/request'

const meetingList = ref([])
const loading = ref(false)
const showAddDialog = ref(false)
const showAuditDialog = ref(false)
const showNotifyDialog = ref(false)
const editingMeeting = ref(null)
const auditingMeeting = ref(null)
const searchKeyword = ref('')
const statusFilter = ref('')
const createTimeRange = ref([])
const currentPage = ref(1)
const pageSize = ref(10)
const total = ref(0)
const uploadUrl = ref('/api/file/upload')
const auditStatus = ref(1)
const publishedMeetings = ref([])

const user = computed(() => {
  const userStr = localStorage.getItem('user')
  return userStr ? JSON.parse(userStr) : {}
})

const isAdmin = computed(() => {
  return user.value.role === 'ADMIN'
})

const meetingForm = reactive({
  meetingName: '',
  meetingTime: '',
  location: '',
  organizer: '',
  category: '',
  meetingContent: '',
  coverImage: ''
})

const notifyForm = reactive({
  title: '',
  content: '',
  meetingId: null
})

const meetingFormRef = ref()

onMounted(() => {
  loadMeetings()
  loadPublishedMeetings()
})

const loadMeetings = async () => {
  loading.value = true
  try {
    const params = {
      current: currentPage.value,
      size: pageSize.value,
      keyword: searchKeyword.value
    }
    
    // 
    if (statusFilter.value !== '') {
      params.status = statusFilter.value
    }
    
    // 
    if (createTimeRange.value && createTimeRange.value.length === 2) {
      // 
      const startDate = createTimeRange.value[0]
      const endDate = createTimeRange.value[1]
      
      if (startDate instanceof Date) {
        params.startDate = startDate.toISOString().split('T')[0]
      } else {
        params.startDate = startDate
      }
      
      if (endDate instanceof Date) {
        params.endDate = endDate.toISOString().split('T')[0]
      } else {
        params.endDate = endDate
      }
    }
    
    const res = await request.get('/meeting/page', { params })
    meetingList.value = res.data.records || []
    total.value = res.data.total || 0
  } catch (error) {
    console.error(':', error)
    meetingList.value = []
  } finally {
    loading.value = false
  }
}

const getStatusType = (status) => {
  const types = { 0: 'warning', 1: 'success', 2: 'danger' }
  return types[status] || 'info'
}

const getStatusText = (status) => {
  const texts = { 0: '', 1: '', 2: '' }
  return texts[status] || ''
}

const editMeeting = (meeting) => {
  editingMeeting.value = meeting
  Object.assign(meetingForm, meeting)
  showAddDialog.value = true
}

const saveMeeting = async () => {
  try {
    const formData = { ...meetingForm }
    
    // 
    if (formData.meetingTime) {
      if (formData.meetingTime instanceof Date) {
        formData.meetingTime = formData.meetingTime.toISOString().slice(0, 19).replace('T', ' ')
      }
    }
    
    if (editingMeeting.value) {
      await request.put(`/meeting/${editingMeeting.value.id}`, formData)
      ElMessage.success('')
    } else {
      await request.post('/meeting', formData)
      ElMessage.success('')
    }
    showAddDialog.value = false
    resetForm()
    loadMeetings()
  } catch (error) {
    console.error(':', error)
  }
}

const deleteMeeting = async (meeting) => {
  try {
    await ElMessageBox.confirm('', '')
    await request.delete(`/meeting/${meeting.id}`)
    ElMessage.success('')
    loadMeetings()
  } catch (error) {
    if (error !== 'cancel') {
      console.error(':', error)
    }
  }
}

const resetForm = () => {
  editingMeeting.value = null
  Object.assign(meetingForm, {
    meetingName: '',
    meetingTime: '',
    location: '',
    organizer: '',
    category: '',
    meetingContent: '',
    coverImage: ''
  })
}

const handleCoverSuccess = (response) => {
  if (response.code === 200) {
    meetingForm.coverImage = response.data
    ElMessage.success('')
  } else {
    ElMessage.error('')
  }
}

const beforeUpload = (file) => {
  const isImage = file.type.indexOf('image/') === 0
  const isLt2M = file.size / 1024 / 1024 < 2

  if (!isImage) {
    ElMessage.error('!')
    return false
  }
  if (!isLt2M) {
    ElMessage.error(' 2MB!')
    return false
  }
  return true
}

const canEdit = (meeting) => {
  // 
  if (!user.value || !user.value.id || !meeting) {
    return false
  }
  
  // 
  if (user.value.role === 'ADMIN') {
    return true
  }
  
  // 
  return meeting.creatorId === user.value.id
}

const canDelete = (meeting) => {
  // 
  if (!user.value || !user.value.id || !meeting) {
    return false
  }
  
  // 
  if (user.value.role === 'ADMIN') {
    return true
  }
  
  // 
  return meeting.creatorId === user.value.id
}

const canAudit = (meeting) => {
  return user.value.role === 'ADMIN' && meeting.status === 0
}

const auditMeeting = (meeting) => {
  auditingMeeting.value = meeting
  auditStatus.value = 1
  showAuditDialog.value = true
}

const submitAudit = async () => {
  try {
    await request.post(`/meeting/${auditingMeeting.value.id}/audit?status=${auditStatus.value}`)
    ElMessage.success('')
    showAuditDialog.value = false
    loadMeetings()
  } catch (error) {
    console.error(':', error)
  }
}

const getImageUrl = (imagePath) => {
  if (!imagePath) return ''
  if (imagePath.startsWith('http')) return imagePath
  return `http://localhost:8080/api${imagePath}`
}

const exportMeetings = async () => {
  try {
    const res = await request.get('/meeting/export', {
      params: {
        keyword: searchKeyword.value
      },
      responseType: 'blob'
    })
    
    const blob = new Blob([res], { type: 'text/csv;charset=utf-8' })
    const url = window.URL.createObjectURL(blob)
    const a = document.createElement('a')
    a.href = url
    a.download = `meetings_${new Date().toISOString().split('T')[0]}.csv`
    document.body.appendChild(a)
    a.click()
    document.body.removeChild(a)
    window.URL.revokeObjectURL(url)
    
    ElMessage.success('')
  } catch (error) {
    console.error(':', error)
    ElMessage.error('')
  }
}

const handleSizeChange = (size) => {
  pageSize.value = size
  currentPage.value = 1
  loadMeetings()
}

// 
const resetFilters = () => {
  searchKeyword.value = ''
  statusFilter.value = ''
  createTimeRange.value = []
  currentPage.value = 1
  loadMeetings()
}

// 
const loadPublishedMeetings = async () => {
  try {
    const res = await request.get('/meeting/page', {
      params: {
        current: 1,
        size: 100,
        status: 1
      }
    })
    publishedMeetings.value = res.data.records || []
  } catch (error) {
    console.error(':', error)
  }
}

// 
const publishNotify = async () => {
  if (!notifyForm.title.trim()) {
    ElMessage.error('')
    return
  }
  if (!notifyForm.content.trim()) {
    ElMessage.error('')
    return
  }
  
  try {
    // 
    let title = notifyForm.title
    let content = notifyForm.content
    
    if (notifyForm.meetingId) {
      const selectedMeeting = publishedMeetings.value.find(m => m.id === notifyForm.meetingId)
      if (selectedMeeting) {
        title = `${notifyForm.title}`
        content = `"${selectedMeeting.meetingName}"\n\n${notifyForm.content}`
      }
    } else {
      title = `${notifyForm.title}`
    }
    
    await request.post('/news', {
      title: title,
      content: content,
      summary: content.substring(0, 200) + (content.length > 200 ? '...' : ''),
      category: '',
      status: 1
    })
    ElMessage.success('')
    showNotifyDialog.value = false
    resetNotifyForm()
  } catch (error) {
    console.error(':', error)
    ElMessage.error('')
  }
}

// 
const openNotifyDialog = () => {
  resetNotifyForm()
  showNotifyDialog.value = true
}

// 
const resetNotifyForm = () => {
  notifyForm.title = ''
  notifyForm.content = ''
  notifyForm.meetingId = null
}
</script>

<style scoped>
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.search-bar {
  margin-bottom: 20px;
}

.search-bar .el-row {
  align-items: center;
}

.pagination {
  margin-top: 20px;
  text-align: right;
}

.empty-data {
  padding: 40px 0;
}
</style> 